<template>
  <el-form :inline="true" :model="query">
    <el-form-item label="Username">
      <el-input v-model="query.username" placeholder="Username"></el-input>
    </el-form-item>
    <el-form-item label="Password">
      <el-input v-model="query.password" placeholder="Password"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="getPageByCondition">Search</el-button>
    </el-form-item>
    <el-form-item>
      <el-button type="success" @click="showDialogAdd">New</el-button>
    </el-form-item>
  </el-form>

  <el-table :data="userList" style="width: 100%">
    <el-table-column prop="id" label="ID" width="50" />
    <el-table-column prop="username" label="Username" width="500" />
    <el-table-column prop="password" label="Password" width="500" />
    <el-table-column>
      <template #default="scope">
        <el-button type="warning" @click="showDialogEdit(scope.row)"
          >Edit</el-button
        >
        <el-button type="danger" @click="showDialogDelete(scope.row)"
          >Delete</el-button
        >
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    class="pagination"
    :currentPage="pagination.currentPage"
    :page-size="pagination.pageSize"
    :total="pagination.total"
    @current-change="handleCurrentChange"
    layout="total, prev, pager, next, jumper"
  ></el-pagination>

  <el-dialog v-model="dialogAddVisible" title="New">
    <el-form :inline="true" :model="formData">
      <el-form-item label="Username">
        <el-input v-model="formData.username"></el-input>
      </el-form-item>
      <el-form-item label="Password">
        <el-input v-model="formData.password"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">Cancel</el-button>
        <el-button type="primary" @click="checkDialogAdd">OK</el-button>
      </span>
    </template>
  </el-dialog>

  <el-dialog v-model="dialogEditVisible" title="Edit">
    <el-form :inline="true" :model="formData">
      <el-form-item label="Username">
        <el-input v-model="formData.username"></el-input>
      </el-form-item>
      <el-form-item label="Password">
        <el-input v-model="formData.password"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancel">Cancel</el-button>
        <el-button type="primary" @click="checkDialogEdit">OK</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import axios from "axios";
import { ElMessage, ElMessageBox } from "element-plus";

export default {
  data() {
    return {
      userList: [],
      query: {},

      formData: {},
      dialogAddVisible: false,
      dialogEditVisible: false,

      pagination: {
        currentPage: 1,
        pageSize: 10,
        total: 1,
      },
    };
  },
  created() {
    this.getPageByCondition();
  },
  methods: {
    getAll() {
      axios.get("/background/users").then((res) => {
        if (res.data.code === 0) this.userList = res.data.data;
        else ElMessage.error(res.data.msg);
      });
    },
    getPageByCondition() {
      axios
        .get(
          "/background/users/" +
            this.pagination.currentPage +
            "/" +
            this.pagination.pageSize,
          {
            params: this.query,
          }
        )
        .then((res) => {
          if (res.data.code === 0) {
            this.userList = res.data.data.records;
            this.pagination.total = res.data.data.total;
            this.pagination.currentPage = res.data.data.current;
            this.pagination.pageSize = res.data.data.size;
          } else ElMessage.error(res.data.msg);
        });
    },
    cancel() {
      this.dialogAddVisible = false;
      this.dialogEditVisible = false;
      this.formData = {};
    },
    showDialogAdd() {
      this.dialogAddVisible = true;
      this.formData = {};
    },
    checkDialogAdd() {
      axios
        .post("/background/users", this.formData)
        .then((res) => {
          if (res.data.code === 0) {
            this.dialogAddVisible = false;
            ElMessage.success(res.data.msg);
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    showDialogEdit(row) {
      this.formData = {};
      axios
        .get("/background/users/" + row.id)
        .then((res) => {
          if (res.data.code === 0) {
            this.formData = res.data.data;
            this.dialogEditVisible = true;
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    checkDialogEdit() {
      axios
        .put("/background/users", this.formData)
        .then((res) => {
          if (res.data.code === 0) {
            this.dialogEditVisible = false;
            ElMessage.success(res.data.msg);
          } else ElMessage.error(res.data.msg);
        })
        .finally(() => {
          this.getPageByCondition();
        });
    },
    showDialogDelete(row) {
      ElMessageBox.confirm("Confirm Delete?", "Warning", {
        confirmButtonText: "Confirm",
        cancelButtonText: "Cancel",
        type: "warning",
      }).then(() => {
        axios
          .delete("/background/users/" + row.id)
          .then((res) => {
            if (res.data.code === 0) ElMessage.success(res.data.msg);
            else ElMessage.error(res.data.msg);
          })
          .finally(() => {
            this.getPageByCondition();
          });
      });
    },
    handleCurrentChange(currentPage) {
      this.pagination.currentPage = currentPage;
      this.getPageByCondition();
    },
  },
};
</script>

<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}

.pagination {
  margin-top: 10px;
  float: right;
}
</style>
